.product-popularity {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}

.product-popularity__title {
  @include centring(0 auto, 283px);
  @include text-style(24px, 30px);

  color: $black;
  text-align: center;
  font-weight: 700;
}

.product-popularity__causes {
  @include centring(0 auto, 242px);
  @include text-style(12px, 20px);

  padding-top: 24px;

  color: $black;
  font-weight: 700;
  text-transform: uppercase;
}

.product-popularity__causes-list {
  @include centring(0 auto, 282px);

  padding: 0;
  padding-top: 5px;
  padding-bottom: 29px;
  padding-left: 28px;

  list-style: none;
}

.product-popularity__description {
  @include text-style(14px, 22px);

  position: relative;

  margin: 0;
  padding-top: 14px;
  padding-bottom: 0;

  color: $black;

  &::before {
    content: "";

    position: absolute;
    top: 18px;
    left: -21px;

    display: block;
    width: 10px;
    height: 14px;

    background-image: url("../img/strelka.png");
    background-repeat: no-repeat;
  }
}

.product-popularity__background {
  padding-bottom: 244px;

  background-image: url("../img/coffee.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.product-popularity__countries {
  @include centring(0 auto, 245px);
  @include text-style (14px, 24px);

  padding-top: 37px;
  padding-bottom: 34px;

  color: $black;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .product-popularity {
    margin-top: 140px;
  }

  .product-popularity__title {
    @include text-style(30px, 40px);

    width: 538px;

    font-weight: 700;
  }

  .product-popularity__causes {
    @include text-style(14px, 20px);

    padding-top: 38px;
    width: 289px;

    font-weight: 700;
  }

  .product-popularity__causes-list {
    padding-top: 18px;
    padding-bottom: 40px;
    width: 461px;
  }

  .product-popularity__causes-item {
    padding-bottom: 7px;
  }

  .product-popularity__description {
    @include text-style(16px, 22px);

    &::before {
      content: "";

      top: 31px;
      left: -49px;
    }
  }

  .product-popularity__background {
    padding-top: 401px;

    background-image: url("../img/coffee-tablet.jpg");
    background-size: cover;
  }

  .product-popularity__position-wrapper {
    position: relative;

    margin: 0 auto;
  }

  .product-popularity__block-wrapper {
    position: absolute;
    bottom: -89px;
    left: -301px;

    padding-top: 20px;
    padding-bottom: 20px;
    width: 601px;

    background-color: $white;
  }

  .product-popularity__countries {
    @include text-style (16px, 24px);

    box-sizing: border-box;
    padding-top: 35px;
    padding-left: 43px;
    padding-right: 50px;
    width: 560px;

    text-align: left;

    border: 1px solid #c8dcba;
    border-bottom: none;
  }
}
